import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { RequestProvider } from '../../providers/request/request';
import { ControllerProvider } from '../../providers/controller/controller'
import { LoginPage } from '../login/login';
import { LandingPage } from '../landing/landing';

@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {
  loading: any;
  regData = { username:'', 
              password1:'', 
              password2:'', 
              email:'',
              first_name:'',
              last_name:'',
              mobile:'',
              group:'',
            };

  constructor(public navCtrl: NavController, 
              public navParams: NavParams, 
              public request: RequestProvider,
              public controller: ControllerProvider
              ) {}

  doSignup() {
    this.regData.username = this.regData.email;
    this.controller.presentLoading('Registrando datos, por favor espere');
    let headers = {'Content-Type':  'application/json'};
    this.request.requestsPost('signup', headers, this.regData).then((result) => {
      this.controller.dismissLoading()
      this.navCtrl.setRoot(LoginPage)
    }, (err) => {
      this.controller.dismissLoading();
      this.controller.presentAlert('Error', 'Verifique los datos y vuelva a intentar.');
    });
  }

  goLanding(){
    this.navCtrl.setRoot(LandingPage);
  }

}


