import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LandingPage } from '../landing/landing';
import { LoginPage } from '../login/login';
import { Facebook, FacebookLoginResponse} from '@ionic-native/facebook';



@IonicPage()
@Component({
  selector: 'page-select-login',
  templateUrl: 'select-login.html',
})
export class SelectLoginPage {

  constructor(public navCtrl: NavController, 
              public navParams: NavParams, 
              public facebook: Facebook) {
  }

  user: any
  loginFacebookError: any;

  goLanding(){
    this.navCtrl.push(LandingPage)
  }
  goLogin(){
    this.navCtrl.push(LoginPage)
  }

  onClickFacebook() {
    this.facebook.login(['public_profile', 'email'])
    .then((res: FacebookLoginResponse) => this.getUserInfo(res.authResponse.userID))
    .catch(e => console.log('hola'));
    }

  getUserInfo(userId: string) {
    console.log(this.facebook.getAccessToken())
    this.facebook.api('me?fields=' + ['name', 'email', 'first_name', 'last_name', 'picture.type(large)'].join(), null)
    .then((res: any) => this.setFacebookUserInfo(res))
    .catch(e => this.loginFacebookError(e));
  }

  setFacebookUserInfo(user: any) {
    this.user = user;
    console.log(this.user)
  }
}



