import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController} from 'ionic-angular';
// Pages
import { SignupPage } from '../signup/signup';
import { LandingPage } from '../landing/landing';
import { SelectLoginPage } from '../select-login/select-login';

// Providers
import { RequestProvider } from '../../providers/request/request';
import { ControllerProvider } from '../../providers/controller/controller';
import { UserProvider } from '../../providers/user/user';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  loginData = { username:'', password:'' };
  data: any;

  constructor(public navCtrl: NavController, 
              public loadingCtrl: LoadingController,
              private requestProvider: RequestProvider,
              private controllerProvider: ControllerProvider,
              private user: UserProvider
              ) {}

  doLogin() {
    let headers = {'Content-Type':  'application/json'}
    this.controllerProvider.presentLoading('Cargando, por favor espere');
    this.requestProvider.requestsPost('login', headers, this.loginData).then((result) => {
      this.data = result;
      localStorage.setItem('token', this.data.token);
      this.user.getUserInfo();
      this.controllerProvider.dismissLoading();
      this.navCtrl.setRoot(LandingPage)
    }, (err) => {
      this.controllerProvider.dismissLoading();
      this.controllerProvider.presentToast('Error de credenciales');
    });
  }

  goSignup() {
    this.navCtrl.push(SignupPage);
  }

  ionViewDidLoad(){
    this.user.logout()
  }

  goBack() {
    this.navCtrl.push(SelectLoginPage);
  }
  
}


