import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class RequestProvider {

  constructor(public http: HttpClient) {}

  public domain: string = '192.168.0.78:8000';

  set_url(action: string, vehicle?: string){
    let http = "http://"
    let ws = "ws://"
    let url: string
    if (action === 'login'){
      url = http + this.domain + "/accounts/login/";
    }
    else if (action === 'signup'){
      url = http + this.domain + "/accounts/signup/";
    }
    else if (action === 'user_info'){
      url = http + this.domain + "/accounts/user_info/";
    }
    return url;
  }

  public requestsPost(action: string, headers: any, data){
    let url = this.set_url(action)
    console.log(url)
    return new Promise((resolve, reject) =>{
      const httpOptions = {
        headers: new HttpHeaders(headers)
      };
      this.http.post(url, JSON.stringify(data), httpOptions)
      .subscribe(res => {
       resolve(res); 
      }, (err) => {
        reject(err);
      });
    });
  }

  resquestGet(action, headers){
    let url = this.set_url(action)
    const httpOptions = {
      headers: new HttpHeaders(headers)
    };
    return this.http.get(url, httpOptions)
  }

}



